"use client"

import { useEffect, useState } from "react"
import { Button } from "@/components/ui/button"
import { motion } from "framer-motion"
import Image from "next/image"

export default function Hero() {
  const fullText = "Your Global Ambition, Powered by Filipino Talent."
  const [typed, setTyped] = useState("")
  const [targetText, setTargetText] = useState(fullText)
  const [lang, setLang] = useState<"en" | "ko" | "ja">("en")
  const isEnglish = lang === "en"

  // Detect current Google Translate language and keep in sync
  useEffect(() => {
    const detectLang = (): "en" | "ko" | "ja" => {
      const combo = document.querySelector<HTMLSelectElement>(".goog-te-combo")
      const comboVal = combo?.value as "en" | "ko" | "ja" | undefined
      if (comboVal) return comboVal
      const cookie = document.cookie
      const match = cookie.split(";").find((c) => c.trim().startsWith("googtrans="))
      if (match) {
        const val = match.split("=")[1]
        const parts = val.split("/")
        const to = parts[parts.length - 1] as "en" | "ko" | "ja"
        if (to) return to
      }
      return "en"
    }

    const updateLang = () => setLang(detectLang())
    updateLang()

    const combo = document.querySelector<HTMLSelectElement>(".goog-te-combo")
    combo?.addEventListener("change", updateLang)
    const interval = window.setInterval(updateLang, 2000)

    return () => {
      combo?.removeEventListener("change", updateLang)
      window.clearInterval(interval)
    }
  }, [])

  useEffect(() => {
    // If not English, render full static text and skip animation
    if (!isEnglish) {
      setTyped(targetText)
      return
    }

    let i = 0
    const speed = 145 // ms per character (slower typing)
    const pauseEnd = 2500 // longer pause before repeating
    let timeout: any
    let cancelled = false

    const typeLoop = () => {
      if (cancelled) return
      setTyped(targetText.slice(0, i))
      i += 1
      if (i <= targetText.length) {
        timeout = setTimeout(typeLoop, speed)
      } else {
        // Finished typing: wait, reset, and repeat
        timeout = setTimeout(() => {
          i = 0
          typeLoop()
        }, pauseEnd)
      }
    }

    // Start typing loop
    timeout = setTimeout(typeLoop, speed)

    return () => {
      cancelled = true
      clearTimeout(timeout)
    }
  }, [targetText, isEnglish])

  // Read translated headline from hidden source and update targetText
  useEffect(() => {
    const readTranslatedText = () => {
      const el = document.getElementById("headline-source")
      const text = (el?.textContent || fullText).trim()
      setTargetText(text)
      setTyped("")
    }

    // initial read
    readTranslatedText()

    const combo = document.querySelector<HTMLSelectElement>(".goog-te-combo")
    const onChange = () => setTimeout(readTranslatedText, 300)
    combo?.addEventListener("change", onChange)

    const interval = window.setInterval(readTranslatedText, 3000)

    return () => {
      combo?.removeEventListener("change", onChange)
      window.clearInterval(interval)
    }
  }, [])

  return (
    <section className="relative overflow-hidden px-4 py-24 sm:px-6 lg:px-8">
      <div className="absolute inset-0 -z-10">
        <div className="absolute top-1/3 left-1/2 -translate-x-1/2 -translate-y-1/2 w-96 h-96 rounded-full bg-gradient-to-br from-primary/20 via-accent/15 to-transparent blur-3xl"></div>
        <div className="absolute bottom-0 right-0 w-80 h-80 rounded-full bg-accent/10 blur-3xl"></div>
      </div>

      <div className="mx-auto max-w-6xl">
            <div className="grid md:grid-cols-2 gap-12 items-center">
          <motion.div
            className="text-left"
          >
            <h1 className="mb-8 text-5xl sm:text-6xl lg:text-7xl font-black tracking-tighter text-foreground leading-tight text-balance">
              <span>{typed}</span>
              <span
                className={`ml-1 inline-block align-baseline ${
                  isEnglish && typed.length !== targetText.length ? "opacity-100" : "opacity-0"
                }`}
              >
                |
              </span>
            </h1>

            {/* Hidden source element used for reading the translated full phrase */}
            <span id="headline-source" className="sr-only" aria-hidden="true">
              {fullText}
            </span>

            <p className="mb-12 text-lg text-muted-foreground sm:text-xl text-balance leading-relaxed max-w-2xl">
              For Korean and Japanese companies aiming for the world stage, we provide a dedicated, English-proficient
              team in the Philippines to act as your Global Department.
            </p>

            <div className="flex flex-col gap-4 sm:flex-row items-start">
              <Button size="lg" className="bg-primary hover:bg-primary/90 px-8 text-lg font-semibold">
                Request Free Consultation
              </Button>
              <Button
                size="lg"
                variant="outline"
                className="border-accent/50 hover:bg-accent/10 px-8 text-lg font-medium text-accent bg-transparent"
              >
                Schedule Demo
              </Button>
            </div>

            <div className="mt-16 pt-8 border-t border-border/40">
              <p className="mb-6 text-sm font-semibold text-accent uppercase tracking-wide">Global Impact</p>
              <div className="grid grid-cols-3 gap-8">
                {[
                  { stat: "180%", label: "Lead Growth" },
                  { stat: "4", label: "New Partners" },
                  { stat: "3x", label: "Faster Sales" },
                ].map((item) => (
                  <motion.div key={item.stat} whileHover={{ scale: 1.05 }} className="text-center">
                    <div className="text-3xl font-black text-primary mb-1">{item.stat}</div>
                    <p className="text-xs text-muted-foreground">{item.label}</p>
                  </motion.div>
                ))}
              </div>
            </div>
          </motion.div>

          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.8, delay: 0.2 }}
            className="relative hidden md:block"
          >
            <div className="relative w-full h-96 rounded-2xl overflow-hidden border border-accent/30 shadow-2xl">
              <Image
                src="/filipino-professionals-having-video-conference-wit.jpg"
                alt="Filipino professionals in global business meeting"
                fill
                className="object-cover"
              />
              <div className="absolute inset-0 bg-gradient-to-tr from-primary/20 to-transparent"></div>
            </div>
          </motion.div>
        </div>
      </div>
    </section>
  )
}
